<template>
  <div class="container">
    <h1>Latest photos</h1>
    <div class="gallery">
      <GalleryItem v-for="photo in photos" :key="photo.id" :photo="photo" />
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import GalleryItem from './GalleryItem';
import '../assets/css/custom.scss';

export default {
  name: 'Gallery',

  components: {
    GalleryItem,
  },

  data() {
    return {
      photos: [],
    };
  },

  created: function() {
    axios.get('https://api.unsplash.com/photos').then((res) => {
      this.photos = res.data;
      console.log(res.data.toString());
    });
  },
};
</script>

<style lang="scss"></style>
